function loadCloth(modelName, clothType, clothId){
  const loader = new GLTFLoader();
  loader.load(
      `./3DModels/clothes/${modelName}.glb`,
      (gltf) => {
          console.log('success');
          console.log(gltf);
          const cloth = gltf.scene.children[0];
          scene.add(cloth);
          cloth.name = clothType;
          
          if (clothType == ""){
            tshirtCounter += 1;
            tshirtId = clothId;
          }
          if (clothType == ""){
            coatCounter += 1;
            coatId = clothId;
          }
          if (clothType == ""){
            pantsCounter += 1;
            pantsId = clothId;
          }

      },
      (progress) => {
          // console.log('progess');
          // console.log(progress);
      },
      (error) => {
          console.log('error');
          console.log(error);
      },
  );
}

